<template>
  <div class="expertCell">
      <!-- <img :src="getUserAvatar" /> -->
      <div class="user">
        <span>{{itemData.title}}</span>
        <div><i v-for="item in getLabel">{{item}}</i></div>
      </div>

      <!-- <span class="btn" @click.stop.prevent="follow()">关注</span> -->
      <!-- <div class="images" v-if="itemData.imgs.length"><img :src="getImgs" /></div> -->
      <!-- <h3>{{itemData.q_title}}</h3>-->
      <p>关注时间:{{itemData.add_time |formatDate}}</p>
  </div>
</template>

<script>
import {getStore,imgBaseUrl} from '../../../components/common/mUtils'
import { Toast,Indicator,MessageBox} from 'mint-ui'
export default {
  data(){
    return{
      imgBaseUrl,
      userID:'',
      userToken:'',
      data:[],
      isLogin:false,
    }
  },
  props:['itemData'],
  filters:{
    parseFloat(a){
      return parseFloat(a)
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    // console.log(this.userID)
    if (this.userID) {
      this.isLogin = true
    }
  },
  computed:{
    getUserAvatar:function(){
      if(this.itemData){
        if (this.itemData.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          return imgBaseUrl + this.itemData.avatarPath
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getLabel(){
      if (this.itemData.tags) {
        let imags = this.itemData.tags.split(',')
        return imags;
      }else {
        return []
      }

    }
  },
  mounted(){
  },
  components:{
  },
  methods: {
    follow(){
      if (!this.validateLogin()) {
        return
      }
      Indicator.open('提交中...');
      let param = {
        'itemid':this.itemData.id,
      }
      // let getUrl = this.CFWConfig.APIHost + '/v3/ask/dian?token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/gzzj?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        console.log(response)
        let code = response.data.code
        if (code == 200) {
          //this.getIsFollow()
          //this.getData()
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    validateLogin(){
      if (!this.isLogin) {
        MessageBox.confirm('请先登录,确定登录吗？').then(({ action }) => {
          this.$router.push('/login')
        }, response => {
        });
        return false
      }else {
        return true
      }
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
  }

}

</script>

<style scoped>
/***** 问题列表 *****/
.expertCell{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 10px;
  border-bottom: 1px solid #e8e8e8;
  /*border-top: 1px solid #eee;*/
  background-color: #fff;
}
/*.ask_list li{border-bottom: 1px solid #eee;}*/
.expertCell .images{}
.expertCell  img{
  width: 60px;
  height: 60px;
  margin-right: 20px;
  border-radius: 30px;
}

.expertCell .user{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-grow: 1;
}

.expertCell .user span{
  color: #000;
  font-weight: 700;
  text-align: left;
  margin-bottom: 3px;
  line-height: 20px;
}

.expertCell .user div{
  text-align: left;
  font-size: 13px;
  line-height: 30px;
  /*margin-left: 5px;*/
  font-weight: bold;
  font-style: normal;
}

.expertCell .user div i{
  font-style: normal;
  /*margin-left: 0px;*/
  padding: 3px 6px;
  color: #849aae;
  background-color:#f4f7f9;
  margin-right: 10px;
}

.expertCell h3{
  font-size: 16px;
  color: #000;
  font-weight: 400;
  height: 36px;
  line-height: 36px;
  text-align: left;
  /*margin-top: 10px;*/
  font-weight: bold;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:1;
}

.expertCell p{
  text-align: left;
  font-size: 14px;
  line-height: 20px;
  /*background-color: #f7f7f7;*/
  padding:  3px 0px 0px;
  overflow:hidden;
  text-overflow:ellipsis;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:3;
  color: rgb(50,50,50);
}

.expertCell .btn{
  border: 1px solid #31a4fc;
  background-color: #31a4fc;
  font-size: 13px;
  padding: 5px 15px;
  border-radius: 2px;
  color: #fff;
  font-weight: bold;
}


</style>



