<template>
  <div id="baike_pc">
    <PCHeader></PCHeader>

    <section class="nav">
      <PCCommonNav :tid="5"></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <!-- 用户信息 -->
        <section class="userInfo">
          <div class="left" v-if="userInfo">
            <div class="avatar">
              <img :src="getUserAvatar" :data="getUserAvatar">
              <img v-if="userInfo.ifzj" class="icon" src="../../images/main/v_tag.png">
              <div class="edit">
                <CFWUpload
                  flag="'thumbnail'"
                  @complete="uploadComplete"
                  @progress="uploadProgress" >
                  <span>编辑</span>
                </CFWUpload>
              </div>
            </div>
            <div class="info" v-if="!isEdit">
              <span>
                {{userInfo.trueName}}<i v-for="labl in getLabel" v-if="userInfo.ifzj">{{labl}}</i>
                <em @click="setIsEdit()">修改</em>
              </span>
              <p>{{userInfo.signContent}}</p>
            </div>
            <!-- 修改 -->
            <div class="info" v-if="isEdit">
              <span>
                <input v-model="userInfo.trueName" />
                <input v-model="userInfo.tags" placeholder="多个以','隔开" v-if="userInfo.ifzj" />
                <em @click="upUserInfo()">保存</em>
              </span>
              <p><textarea v-model="userInfo.signContent"></textarea></p>
            </div>

          </div>
          <div class="count">
            <span><i>{{tongji.qcount}}</i><em>我的提问</em></span>
            <span class="BLR"><i>{{tongji.rcount}}</i><em>我的回答</em></span>
            <span><i>{{tongji.ccount}}</i><em>我的关注</em></span>
          </div>
        </section>

        <!-- 我的 -->
        <section class="my">
          <div class="myNav">
            <div class="L">
              <nav @click="setMyNav(1)">
                <div><span :class="myNavId == 1 ? 'selectd':'' ">我的提问</span></div>
              </nav>

              <nav @click="setMyNav(2)">
                <div class="BLR" :class="myNavId == 2 ? 'selectd':'' "><span>我的回答</span></div>
              </nav>

              <nav @click="setMyNav(3)">
                <div><span :class="myNavId == 3 ? 'selectd':'' ">我的关注</span></div>
              </nav>

              <nav @click="setMyNav(4)">
                <div class="BL" :class="myNavId == 4 ? 'selectd':'' "><span>向我提问</span></div>
              </nav>
            </div>

            <div class="R" v-if="myNavId == 3">
              <span v-for="item in followTab" @click="setFollowType(item)">
                <img v-if="followType == item.id" src="../../images/main/radio_icon_s.png">
                <img v-if="followType != item.id" src="../../images/main/radio_icon.png">
                {{item.name}}
              </span>
            </div>

          </div>

          <!-- 我的提问 -->
          <div class="myAskList c_list" v-if="myNavId == 1">
            <li v-for="item in myAsk.list">
              <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
              <MyAskList :itemData="item" @delAction = "delAction" :type="1"></MyAskList>
              </router-link>
            </li>
            <!-- <CFWPage
              @gotoPage="gotoPage"
              :pages="8">
            </CFWPage> -->
            <div class="noData" v-if="myAsk.list.length == 0">
              <img src="../../images/main/noData_icon.png"/>
              <span>暂时没有内容哟~</span>
            </div>
          </div>

          <!-- 我的回答 -->
          <div class="myAnswerList c_list" v-if="myNavId == 2">
            <li v-for="item in myAnswer.list">
              <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
              <MyAnswerList :itemData="item" @delAnswerAction="delAnswerAction" :type="1"></MyAnswerList>
              </router-link>
            </li>
          </div>

          <!-- 我的关注 -->
          <div class="myFollow c_list" v-if="myNavId == 3">
            <div v-if="followType == 2">
              <li v-for="item in expertList">
                <ExpertFollowCell :itemData="item"></ExpertFollowCell>
              </li>
            </div>

            <div v-if="followType == 1">
              <li v-for="item in followAskList">
                <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
                <FollowList :itemData="item" ></FollowList>
                </router-link>
              </li>
            </div>
          </div>

          <!-- 向我提问-->
          <div class="myAnswerList c_list" v-if="myNavId == 4">
            <li v-for="item in toMyList">
              <router-link :to="{path: '/askDetail_pc', query:{qid: item.q_id}}">
              <MyAskList :itemData="item" @delAction = "delAction" :type="2"></MyAskList>
              </router-link>
            </li>
            <div class="noData" v-if="toMyList.length == 0">
              <img src="../../images/main/noData_icon.png"/>
              <span>暂时没有内容哟~</span>
            </div>
          </div>

        </section>

      </div>
      <div class="right">
        <section class="newask">
          <!-- <div class="PCheader_title"><h2 style="margin-left:10px;">专家解答</h2></div>
          <div class="item">
              <span  v-for="item,index in askDec"><i>{{index+1}}</i>{{item.name}}</span>
          </div> -->
          <PCNewAskList></PCNewAskList>
        </section>

      </div>
    </section>

    <PCFooter></PCFooter>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl,upImgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import MyAskList from './pc/PCMyAskList'
import MyAnswerList from './pc/PCMyAnswerList'
import ExpertFollowCell from './pc/PCExpertFollowCell'
import FollowList from './pc/PCFollowList'
import CFWUpload from '../../components/common/CFWUpload'
import PCNewAskList from './pc/PCNewAskList'
import PCCommonNav from '../../components/common/PCCommonNav'
import CFWPage from '../../components/common/CFWPage'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      userID:'',
      userToken:'',
      isLogin:false,
      lbArr:[],
      list:[],
      myAskList:[],  // 我的提问
      // 我的提问
      myAsk:{
        list:[], // 列表
        curPage:1, // 当前页
        pages:0 // 总页数
      },
      myAnswerList:[], // 我的回答
      myAnswer:{
        list:[], // 列表
        curPage:1, // 当前页
        pages:0 // 总页数
      },
      myAskCurPage:1, // 我的提问当前页
      myAnswerCurPage:1, // 我的回答当前页
      expertList:[], // 我关注的专家
      followAskList:[], // 我关注的问题
      curPage:1, // 当前页
      pageSize:10, // 默认显示10数据
      myNavId:1,
      typeList:[],
      userInfo:null,
      askDec:[
        {name:'描述现象'},
        {name:'不要加工'},
        {name:'言简意赅'},
        {name:'说明目的'},
      ],
      followTab:[
        {id:1,name:'已关注问题'},
        {id:2,name:'已关注专家'},
      ],
      followType:1,
      searchKey:{
        page:1,
        complete:false,
      },
      isEdit:false,
      tongji:{
        qcount:0,
        rcount:0,
        ccount:0,
      },
      toMyList:[] // 向我提问
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
    }

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
    getNumber(idx){
      ++idx
      if (idx < 10) {
        idx = '0'+idx
      }
      return idx;
    }
  },
  mounted(){
    // 获取用户资料
    this.getUserData()

    // 获取我的问题
    this.getMyAskList()

    // 获取我的回答
    this.getMyAnswerList()

    // 获取我关注的专家
    this.getExpertData()

    // 获取我关注的问题
    this.getFollowAskData()

    // 统计列表
    this.getMytongji()

    this.getToMyList()

  },
  components:{
    PCHeader,
    PCFooter,
    MyAskList, // 问题列表组件
    MyAnswerList, // 回答列表组件
    ExpertFollowCell, // 关注专家组件
    FollowList, // 关注问题组件
    CFWUpload, // 上传图片
    PCNewAskList,
    PCCommonNav,
    CFWPage
  },
  computed:{
    getUserAvatar:function(){
      if(this.userInfo.avatarPath){
        // http://tu.07358.com
        let indexOf = this.userInfo.avatarPath.indexOf('http://tu.07358.com')
        if(indexOf != -1){
          return this.userInfo.avatarPath
        }else {
          return imgBaseUrl + this.userInfo.avatarPath
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    },
    getLabel(){
      if (this.userInfo.tags !='') {
        let imags = this.userInfo.tags.split(',')
        return imags;
      }else {
        return []
      }
    }
  },
  methods: {
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    setIsEdit(){
      this.isEdit = true
    },
    setFollowType(item){
      this.followType = item.id
    },
    setMyNav(idx){
      this.myNavId = idx
    },
    // 获取问题类别
    getAskType(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/lb'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.lbArr = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 获取我的问题列表
    getMyAskList(){
      Indicator.open();
      let pStr = '?curPage='+this.myAsk.curPage+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/mylist'+pStr
      this.myAsk.list = []
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          this.myAsk.list = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 我的回答
    getMyAnswerList(){
      let pStr = '?curPage='+this.myAnswer.curPage+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/myanswer'+pStr
      this.$http.get(getUrl).then(response => {
        this.myAnswer.list = []
        let code = response.data.code
        if (code == 200) {
          this.myAnswer.list = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 关注的专家
    getExpertData(){
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/mygzzj'+pStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.expertList.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    //
    getFollowAskData(){
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/mygzwenti'+pStr
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.followAskList.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    getMytongji(){
      // http://api.07358.com/v3/ask/mytongji?token=CYKVUJ8teDegtOaHabyCwvYIHsfYRLh0
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/mytongji?token='+this.userToken
      this.$http.post(getUrl).then(response => {
        if (response.data.code == 200) {
          this.tongji.qcount = response.data.result.qcount
          this.tongji.rcount = response.data.result.rcount
          this.tongji.ccount = response.data.result.ccount
        }
      }, response => {
        Toast('请求失败');
        console.log(response);
      });

    },
    // 获取向我提问列表
    getToMyList(){
      let pStr = '?curPage='+this.searchKey.page+'&pageSize='+this.pageSize+'&token='+this.userToken
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/tomylist'+pStr
      this.$http.get(getUrl).then(response => {
        if (this.searchKey.page == 1) {
          this.toMyList = []
        }
        let code = response.data.code
        if (code == 200) {
          let arr = response.data.result
          if (arr.length > 0) {
            // 如果小于10条就是加载完了
            this.complete = arr.length < 10 ? true :false
            arr.forEach((item) => this.toMyList.push(item))
          } else {
            this.complete = true
            this.allLoaded = true
          }
        }
        this.searchKey.page++
        this.loading = false // 请求完成
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    // 获取用户资料
    getUserData(){
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/user?token='+this.userToken
      this.$http.post(getUrl).then(response => {
        if (response.data.code == 200) {
          this.userInfo = response.data.result
        }
      }, response => {
        Toast('请求失败');
        console.log(response);
      });
    },
    //  删除回答
    delAnswerAction(item){
      console.log(item)
      if (!this.isLogin) {
        Toast('请先登录！')
        return
      }
      Indicator.open('提交中...');
      let param = {
        'aid':item.r_id,
      }
      //http://api.07358.com/v3/ask/delmyhuida?
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/delmyhuida?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.searchKey.page = 1
          setTimeout(() => {
            this.getMyAnswerList()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 参数转化成表单对象
    objToformData(obj){
      // let params = new URLSearchParams()
      let params = new FormData()
      Object.keys(obj).forEach( (key, i) => {
        params.append(key, obj[key]);
      });
      return params;
    },
    // 上传图片过程中的回调
    uploadProgress (progress, flag) {
        //这里可以通过回调的flag对不同上传域做处理
        this.progress = progress < 100 ? progress : 0;
    },
    // 上传图片完成的回调
    uploadComplete(status, result, flag) {
        if (status == 200) { //
          let imgUrl = upImgBaseUrl + result.key //七牛域名 + 返回的key 组成文件url
          this.userInfo.avatarPath = result.key
          this.upUserInfo()
          //this.askParams.img.push(imgUrl)
        } else {
            //失败处理
        }
    },
    // 删除我的问题
    delAction(params){
      if (!this.isLogin) {
        Toast('请先登录！')
        return
      }
      Indicator.open('提交中...');
      let param = {
        'qid':params.q_id,
      }
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/delmywenti?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(param))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.searchKey.page = 1
          setTimeout(() => {
            this.getMyAskList()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 修改用户资料
    upUserInfo(){
      if (!this.isLogin) {
        Toast('请先登录！')
        return
      }
      Indicator.open('提交中...');
      let params ={
        trueName : this.userInfo.trueName, // 昵称
        signContent : this.userInfo.signContent, // 个性签名
        avatarPath : this.userInfo.avatarPath, // 头像
        tags : this.userInfo.tags, // 标签
      }

      let getUrl = this.CFWConfig.APIHost + '/v3/ask/upuser?token='+this.userToken
      this.axios.post(getUrl,this.objToformData(params))
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          setTimeout(() => {
            this.isEdit = false
            this.getUserData()
          }, 800);
        }
        Toast(response.data.message);
        Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    gotoPage(page){

    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },

}
</script>

